.root {
    margin-top: 30px;
}

.title {
    font-size: 14px;
    font-weight: 500;

    @media(min-width: 1024px) {
		font-size: 16px;
	}
}

.container {
    background-color: #000;
    padding: 15px;
    border-radius: 20px;
    border: 1px solid #fff;
    font-size: 12px;
    color: #fff;
    row-gap: 16px;
    column-gap: 15px;
    font-size: 12px;

	@media(min-width: 1024px) {
		font-size: 14px;
        padding: 15px 30px
	}

    &:not(:last-child) {
        margin-bottom: 16px;
    }
}

.stats {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    line-height: 1.4;
}