.main {
    font-family: "Press Start 2P";
}

.tabs {
	display: flex;
	flex-direction: column;
	row-gap: 30px;

	@media(min-width: 900px) {
		flex-direction: row;
		column-gap: 30px;
	}
}

.tab {
	position: relative;
	width: 100%;
	cursor: pointer;
	will-change: transform;
	transition: transform 0.6s ease;

	@media(min-width: 900px) {
		width: 50%;
	}

	&:hover {
		transform: scale(1.02);
	}

	img {
		border-radius: 60px;
		border: 2px solid #fff;
		max-width: 100%;
	}
}

.label {
	position: absolute;
	z-index: 1;
	font-size: 16px;
	color: #fff;

	@media(min-width: 460px) {
		font-size: 24px;
	}

	@media(min-width: 1024px) {
		font-size: 32px;
	}
}

.nirvanaLabel {
	bottom: 30px;
	left: 50%;
	transform: translateX(-50%);

	@media(min-width: 460px) {
		bottom: 50px;
	}

	@media(min-width: 760px) {
		bottom: 60px;
	}

	@media(min-width: 900px) {
		bottom: 40px;
	}

	@media(min-width: 1024px) {
		bottom: 60px;
	}
}

.r8Label {
	top: 30px;
	left: 30px;

	@media(min-width: 460px) {
		top: 35px;
		left: 35px;
	}

	@media(min-width: 760px) {
		top: 40px;
		left: 40px;
	}

	@media(min-width: 900px) {
		top: 30px;
		left: 30px;
	}

	@media(min-width: 1024px) {
		top: 40px;
		left: 40px;
	}
}

.lateLabel {
	right: 20px;
	top: 30px;

	@media(min-width: 460px) {
		top: 35px;
	}

	@media(min-width: 760px) {
		top: 40px;
		right: 40px;
	}

	@media(min-width: 900px) {
		top: 30px;
		right: 30px;
	}

	@media(min-width: 1024px) {
		top: 40px;
		right: 40px;
	}
}

.info {
	color: #fff;
	margin-top: 50px;
}

.infoTitle {
	margin-bottom: 40px;
	font-size: 24px;
}

.infoText {
	font-size: 14px;
	line-height: 30px;

	&:not(:last-child) {
		margin-bottom: 16px;
	}

	@media(min-width: 1024px) {
		font-size: 16px;
	}
}