.root {
    background-color: #000;
    padding: 15px;
    border-radius: 20px;
    border: 1px solid #fff;
    font-size: 12px;
    color: #fff;
    display: flex;
    row-gap: 16px;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 15px;

	@media(min-width: 460px) {
		font-size: 14px;
	}

	@media(min-width: 1024px) {
        padding: 15px 30px
	}
}

.items {
    display: flex;
    flex-wrap: wrap;
    row-gap: 16px;
    column-gap: 16px;
}

.resLabel {
    display: flex;
    align-items: center;
    column-gap: 8px;

    @media(min-width: 700px) {
        min-width: 306px;
    }
}

.price {
    display: flex;
    align-items: center;
    column-gap: 16px;

    @media(min-width: 700px) {
        border-right: 1px solid #fff;
        padding-right: 16px;
    }
}

input {
    max-width: 150px;
    width: 100%;
    background-color: transparent;
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom: 1px solid rgba(254, 254, 254, .5);
    color: #fefefe;
    font-size: 20px;
    outline: none;
}