.root {
    background-color: #000;
    padding: 15px;
    border-radius: 20px;
    border: 1px solid #fff;
    font-size: 12px;
    color: #fff;
    row-gap: 16px;
    column-gap: 15px;

	@media(min-width: 460px) {
		font-size: 14px;
	}

	@media(min-width: 1024px) {
        padding: 15px 30px
	}
}

.title {
    padding-top: 16px;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;

    @media(min-width: 1024px) {
        font-size: 16px;
    }
}

.image {
    margin-right: 16px;
}

.main {
    margin-top: 60px;

    @media(min-width: 1024px) {
        display: flex;
        flex-wrap: wrap;
        column-gap: 30px;
        justify-content: space-between;
    }

    img {
        width: 75px;
        height: 75px;
        border-radius: 99999px;
    }
}

.left, .right {
    width: 100%;

    @media(min-width: 1024px) {
        width: calc(50% - 15px);
    }
}

.self {
    display: none;

    @media(min-width: 1024px) {
        display: block;
    }
}

.result {
    border-top: 1px solid #fff;
    padding-top: 16px;
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    font-size: 12px;

    @media(min-width: 1024px) {
        font-size: 14px;
    }
}

.clear {
    cursor: pointer;
    text-align: center;
    max-width: 150px;
    padding: 8px;
    border: 2px solid #fff;
    border-radius: 30px;
}

.label {
    line-height: 1.4;
}