.root {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 60px;
}

.title {
    margin-bottom: 60px;
}

.statType {
    &:not(:last-child) {
        margin-bottom: 8px;
    }
}

.statTitle {
    text-align: center;
    padding: 16px;
    border-radius: 30px;
    border: 2px solid #fff;
    display: flex;
    justify-content: center;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 12px;
    max-width: 560px;
    line-height: 1.4;

    @media(min-width: 1024px) {
        font-size: 14px;
    }

    &:hover {
        background-color: rgba(33, 2, 4, 1);
    }
}