::-webkit-scrollbar-track {
    background-color: #000; /*Цвет скролла*/
}
::-webkit-scrollbar-thumb {
    border-radius: 6px; /*Закругление ползунка*/
    background-color: #fff; /*Цвет ползунка*/
}
::-webkit-scrollbar {
    width: 6px; /*Толщина скролла*/
}

.root {
    width: 100%;
}

.top {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
}

.equip, .prices {
    margin-bottom: 20px;
}

.controls {
    margin-top: 30px;
}