.image {
    display: flex;
    row-gap: 16px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12px;

    @media(min-width: 1024px) {
		font-size: 14px;
	}
}

.propsContainer {
    display: flex;
    justify-content: center;
    min-height: 250px;
}

.props {
    max-width: 560px;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 16px;
    margin-top: 60px;
}

.prop {
    text-align: center;
    font-size: 12px;
    line-height: 1.4;

    @media(min-width: 1024px) {
        font-size: 14px;
    }
}