.root {
    color: #fff;
}

.title {
    font-size: 14px;
    text-align: center;
    margin-bottom: 30px;

    @media(min-width: 1024px) {
        font-size: 16px;
    }
}

.items {
    font-size: 12px;

    @media(min-width: 1024px) {
        font-size: 14px;
    }
}

.item {
    padding: 16px;
    cursor: pointer;
    line-height: 1.4;

    &:not(:last-child) {
        margin-bottom: 16px;
    }
}