.root {
    background-color: #000;
    padding: 15px;
    border-radius: 20px;
    border: 1px solid #fff;
    font-size: 12px;
    color: #fff;
    display: flex;
    row-gap: 16px;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 15px;

	@media(min-width: 460px) {
		font-size: 14px;
	}

	@media(min-width: 1024px) {
		font-size: 16px;
        padding: 15px 30px
	}
}

.items {
    display: flex;
    flex-wrap: wrap;
    row-gap: 16px;
    column-gap: 16px;
}