.root {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 16px;

    @media(min-width: 1024px) {
        flex-direction: row;
        column-gap: 16px;
        justify-content: space-between;
    }
}

.button {
    min-width: 200px;
    cursor: pointer;
    display: inline-flex;
    justify-content: center;
    font-size: 12px;
    color: #fff;
    text-decoration: none;
    padding: 10px 30px;
    background-color: #000;
    border-radius: 20px;
    border: 2px solid #fff;
    transition: background-color 0.3s ease;

	@media(min-width: 460px) {
		font-size: 14px;
	}

    &:hover {
        background-color: rgba(33, 2, 4, 1);
    }
}

.auto {
    order: 2;

    @media(min-width: 1024px) {
        order: 0;
    }
}