.root {
    font-size: 28px;
    line-height: 40px;
    color: #fff;
    display: flex;
    justify-content: center;
    margin: 0 0 30px;
    text-align: center;

    @media(min-width: 460) {
        font-size: 32px;
    }

    @media(min-width: 760px) {
        font-size: 38px;
    }

    @media(min-width: 1024px) {
        font-size: 48px;
        text-align: inherit;
        line-height: inherit;
        margin: 0 0 50px;
    }
}