.root {
    display: inline-block;
    font-size: 12px;
    color: #fff;
    text-decoration: none;
    padding: 10px 30px;
    background-color: #000;
    border-radius: 20px;
    border: 2px solid #fff;
    transition: background-color 0.3s ease;

	@media(min-width: 460px) {
		font-size: 14px;
	}

    &:hover {
        background-color: rgba(33, 2, 4, 1);
    }
}