.root {
	font-family: "Press Start 2P";
	min-height: calc(100vh - 30px);
	padding: 20px 30px 60px;
	display: flex;
	justify-content: center;

	&.gradient {
		background: rgb(33,2,4);
		background: linear-gradient(90deg, rgba(33,2,4,1) 0%, rgba(0,0,0,1) 50%, rgba(2,0,24,1) 100%);
	}
}

.container {
	width: 100%;
	max-width: 1600px;
}

.header {
    font-family: "Press Start 2P";
    margin-bottom: 30px;
	color: #fff;
	font-size: 12px;

	@media(min-width: 460px) {
		font-size: 14px;
	}

	@media(min-width: 1024px) {
		font-size: 16px;
	}
}

.background {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	z-index: -1;
}