.root {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 60px;
}

.title {
    margin-bottom: 60px;
}

.label {
    font-size: 12px;
    line-height: 1.4;

    @media(min-width: 1024px) {
        font-size: 14px;
    }

    &:not(:last-child) {
        margin-bottom: 16px;
    }
}