.root {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(2px);
}

.container {
    width: 60%;
    min-width: 300px;
    min-height: 320px;
    max-height: 80%;
    overflow-y: auto;
    padding: 16px;
    background-color: #000;
    border-radius: 30px;
    border: 2px solid #fff;
}

.close {
    color: #fff;
    font-size: 24px;
    display: flex;
    justify-content: flex-end;
}

.closeButton {
    cursor: pointer;
}